






















































































































import service from '../../service.js';
import sharebtn from '../../components/shareBox.vue';
export default {
	data() {
		return {
			id:'',
			username:'',
			phone:'',
			sex:'',
			quan_num:0,
			head_img:'',
			imgList: [],
			corporateList:[], 	//企业形象
			aptitudeList:[],	// 资质证书
			logoList:[],		// logo
			picSize:10, 		// 照片的最大张数
			addressurl:"",
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../../static/location.png'
			}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../../static/location.png'
			}],
			content:'',
			BASEURL:'',
			backgroundImage:'http://lost.jade2707.top/static/home/images/mine/bg.jpg',
			caseList:[],
		};
	},
	components:{
		sharebtn
	},
	onLoad(option) {			
		if(option.id){
			this.id = option.id;
		}else{
			uni.showToast({
				title:'分享已过期，请重试',
				icon:'none'
			})
		}
	},
	mounted(){
		
		let self = this;
		this.BASEURL = service.BASEURL;
		
		console.log('hasLogin',this.hasLogin);
			// 获取用户信息 保存到vuex中
			uni.request({
				url:service.BASEURL+'/home/share/ditails',
				method:"POST",
				data:{
					id:this.id
				},
				success(res) {
					console.log(res);
					if(!res.data || res.data.code != 1){
							uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}else{
						let info = res.data.data;
						
						self.username = info.name;
						self.phone = info.mobile;
						self.content = info.content;
						self.quan_num = info.quan_num;
						if(info.head_img){
							self.head_img =service.BASEURL+ info.head_img;
						}
						
						if(info.corporate){
							self.corporateList = info.corporate.map(e=>(service.BASEURL+e.url))
							// 更换背景图片
							self.backgroundImage = self.corporateList[0];
						}
						
						if(info.aptitude){
							self.aptitudeList = info.aptitude.map(e=>(service.BASEURL+e.url))
						}
						self.addressurl = info.addressurl;
					}
				}
			})
			
			let data2 = {};
			uni.request({
				url:service.BASEURL+'/home/share/case_list',
				method:"POST",
				data:{
					company:this.id
				},
				success(res) {
					console.log(res);
					if(!res.data || res.data.code != 1){
							uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}else{
						self.caseList = res.data.data.list;
					}
				}
			})
	},
	methods: {
		gotoURL(url) {
			uni.redirectTo({					
				url:url
			})
		},
		gotoURLWithNav(url){
			
		},
		showCaseDetail(id){
			
			uni.navigateTo({
				url:`./casedetail/casedetail?id=${id}`
			})
		},
		showMoreCase(){
			// 查看更多案例
			console.log('showMoreCase')
			uni.navigateTo({
				url:`./caselist/caselist?id=${this.id}`
			});
		},
		editcompanyinfo(){
			console.log('editcompanyinfo')				
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		ViewCorporateImage(e){
			uni.previewImage({
				urls: this.corporateList,
				current: e.currentTarget.dataset.url
			});
		},
		ViewAptitudeListImage(e){
			uni.previewImage({
				urls: this.aptitudeList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '提示',
				content: '确定要删除图片吗？',
				cancelText: '返回',
				confirmText: '删掉',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		ChooseImage() {
			let self = this;
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res1) => {
					const tempFilePaths = res1.tempFilePaths;
					if (self.imgList.length != 0) {
						self.imgList = self.imgList.concat(res1.tempFilePaths)
					} else {
						self.imgList = res1.tempFilePaths
					}
				}
			});
		},
	},
}
